html, body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  touch-action: none;
  overflow: hidden;
}

body {
  background-color: #f1f1f1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #000;
}

@media (min-width: 480px) {
  body {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

main {
  width: 100%;
  height: 100%;
  max-width: 480px;
  max-height: 853px;
  margin: 0 auto;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: stretch;
}

section {
  width: 100%;
  height: 100%;
  flex: 1 0 100%;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

audio {
  display: none;
  visibility: none;
  opacity: 0;
  height: 0;
  width: 0;
}

video {
  display: block;
  width: 100%;
  touch-action: none;
}

canvas {
  display: block;
  width: 100%;
  height: 100%;
}

video::-webkit-media-controls-play-button,
video::-webkit-media-controls-start-playback-button {
  opacity: 0;
  pointer-events: none;
  width: 5px;
}

section:nth-child(odd) {
  background-color: #FFFC00;
}

section:nth-child(even) {
  background-color: #fff;
}

#video {
  display: none;
}

.launcher {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.launcher button {
  display: none;
  border: 2px solid #000;
  background-color: #fff;
  font-size: 1.5rem;
  padding: 1rem 2rem;
}

.snap-cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  transition: all 300ms ease-in-out;
  background-color: #000;
}

.snap-cover.over {
  top: -130%;
}

.snap-content {
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.snap-content:not(.video) {
  padding: 1rem;
}

.content-more .snap-cover::before, .content-more .snap-cover::after {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0%);
  text-shadow: 0 0 10px #000;
  color: #fff;
  text-align: center;
  width: 60px;
  font-size: 1.1rem;
}

.content-more .snap-cover::before {
  content: "âŒƒ";
  bottom: 15px;
}

.content-more.video .snap-cover::after {
  content: "WATCH";
}

.content-more.html .snap-cover::after {
  content: "READ";
}

.progress {
  border: 2px solid #000;
  background-color: #fff;
  width: 200px;
  padding: 0 1rem;
}

.progress-bar {
  height: 10px;
  width: 0%;
  margin: 1rem 0;
  background-color: #000;
}
