@import 'normalize';
@import 'typebase';
@import 'base';

section:nth-child(odd){
  background-color: #FFFC00;
}
section:nth-child(even){
  background-color: #fff;
}
#video{
  display: none;
}

.launcher{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  button{
    display: none;
    border: 2px solid #000;
    background-color: #fff;
    font-size: 1.5rem;
    padding: 1rem 2rem;
  }
}

.snap{
  &-cover{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    transition: all 300ms ease-in-out;
    background-color: #000;
  }

  &-cover.over{
    top: -130%;
  }
  &-content{
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    &:not(.video){
      padding: 1rem;
    }
  }
}

.content-more{
  .snap-cover{
    &::before,
    &::after{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 0%);
      text-shadow: 0 0 10px #000;
      color: #fff;
      text-align: center;
      width: 60px;
      font-size: 1.1rem;
    }
    &::before{
      content: "âŒƒ";
      bottom: 15px;
    }
  }
  &.video{
    .snap-cover::after{
      content: "WATCH";
    }
  }
  &.html{
    .snap-cover::after{
      content: "READ";
    }
  }
}

.progress{
  border: 2px solid #000;
  background-color: #fff;
  width: 200px;
  padding: 0 1rem;
  &-bar{
    height: 10px;
    width: 0%;
    margin: 1rem 0;
    background-color: #000;
  }
}